/* harmony default export */ __webpack_exports__["a"] = ({about:{heading:'About Us',paragrapghs:['Supply Change was founded in April 2018 by Aoise Keogan-Nooshabadi, Beth Pilgrim and Verena Wimmer after researching solutions to the gap in social enterprises in public sector supply chains.','We found that despite many housing associations and other public organisations wanting to add social value to their supply chains, current procurement processes and portals made it hard for social enterprises to compete against larger private businesses. We decided to set up Supply Change to create a platform for social enterprises, housing associations and councils to find each other.','Aoise, Verena and Beth are alumni of the Year Here programme, a post grad in social innovation that challenges fellows to find solutions to the UKs toughest social issues. During the programme the team worked across frontline organisations such as housing associations, homeless hostels and schools and saw firsthand the benefits that social enterprises can deliver for different communities.']},howItWorks:{heading:'How it works',paragrapghs:['Supply Change is the UKs first digital marketplace and connects social enterprises to public sector contracts. Supply Change brings together social enterprises, councils and housing associations to help them drive real impact in their local communities. ','We want to put social value at the heart of public sector procurement. Our platform will be coming soon, join us on our mission and sign up for more information below.','Social Enterprises that want to win public sector contracts, can sign up and will be able to create a profile. Once completed, suitable and live contracts will be suggested.','Public sector organisations will be able to search suitable suppliers, invite them to bid and contract their services. ','We take a membership fee from both social enterprises and public sector organisations and also a percentage of any contracts closed on the site.','Our vision is to become the go-to place to find mission driven suppliers for public sector work and to put social value at the heart of public services.']}});

